<template>
<div>
	<h4>v-bind:key Index</h4>
	<div><button v-on:click="initArr()">초기화</button> / <button v-on:click="delArr()">값 삭제</button></div>
	<div>
		<ul>
			<li
				v-for="(item, idx) in testArr"
				v-bind:key="idx">{{item}}</li>
		</ul>
	</div>
</div>
</template>

<script>
import {mapState, mapMutations} from 'vuex';

export default {
	computed: {
		...mapState('step7', {
			'testArr': 'testArr',
		}),
	},
	method: {
		...mapMutations('step7', {
			'testArr': 'testArr',
		}),
	},
}
</script>

<style scoped>
.test_class {
	color: gray;
}
</style>


